export default function GameCardSkeleton() {
  return (
    <div className="bg-slate-800 overflow-hidden rounded-lg animate-pulse h-full flex flex-col border border-slate-700">
      {/* Image Skeleton */}
      <div className="aspect-square bg-slate-700 w-full" />

      {/* Content Skeleton */}
      <div className="p-3 space-y-3 flex-1 flex flex-col">
        {/* Title Skeleton */}
        <div className="space-y-2">
          <div className="h-3 bg-slate-700 rounded w-5/6" />
          <div className="h-3 bg-slate-700 rounded w-4/6" />
        </div>

        {/* Score Skeleton */}
        <div className="h-4 bg-slate-700 rounded w-16" />

        {/* Platforms Skeleton */}
        <div className="flex gap-2 mt-auto">
          <div className="h-5 bg-slate-700 rounded-sm w-12" />
          <div className="h-5 bg-slate-700 rounded-sm w-12" />
        </div>
      </div>
    </div>
  );
}
