/* eslint-disable @next/next/no-img-element */
'use client';

import { useGenres } from '../hooks/useGenres';
import { useGameQuery } from '../context/GameQueryContext';
import type { Genre } from '../entities/Genre';
import { getCroppedImageUrl } from '../services/image-url';

export default function GenreList() {
  const { gameQuery, setGenreId } = useGameQuery();
  const { genres, error, isLoading } = useGenres();
  const selectedGenreId = gameQuery.genre;

  if (error) return null;
  if (isLoading) return <div className="p-4 text-slate-400">Loading genres...</div>;

  return (
    <div className="space-y-4 w-full">
      <h2 className="text-xl font-black text-white mb-6 flex items-center gap-3 pl-2">
        <span>üìÇ</span> Genres
      </h2>
      
      <button
        onClick={() => setGenreId(null)}
        className={`w-full text-left px-4 py-3 rounded-xl transition-all duration-200 flex items-center gap-3 text-sm font-semibold whitespace-nowrap ${
          selectedGenreId === null 
            ? 'bg-violet-600 text-white shadow-lg shadow-violet-500/30' 
            : 'text-zinc-400 hover:text-white hover:bg-white/5'
        }`}
      >
        <span>All Genres</span>
      </button>

      <div className="space-y-1">
        {genres.map((genre: Genre) => (
          <button
            key={genre.id}
            onClick={() => setGenreId(genre.id)}
            className={`w-full text-left px-4 py-2.5 rounded-xl transition-all duration-200 flex items-center gap-3 text-sm font-medium group ${
              selectedGenreId === genre.id 
                ? 'bg-violet-600 text-white shadow-lg shadow-violet-500/30' 
                : 'text-zinc-400 hover:text-white hover:bg-white/5'
            }`}
          >
            <img
              src={getCroppedImageUrl(genre.image_background)}
              alt={genre.name}
              className={`w-8 h-8 rounded-lg object-cover flex-shrink-0 transition-all ${
                selectedGenreId === genre.id
                  ? 'border-2 border-white/20'
                  : 'border border-white/5 group-hover:border-white/20'
              }`}
            />
            <span className="truncate">{genre.name}</span>
          </button>
        ))}
      </div>
    </div>
  );
}
