import type { Game } from '../entities/Game';
import { getCroppedImageUrl } from '../services/image-url';
import CriticScore from './CriticScore';

interface GameCardProps {
  game: Game;
}

export default function GameCard({ game }: GameCardProps) {
  return (
    <div className="bg-[#202020] rounded-xl overflow-hidden hover:scale-105 transition-transform duration-300 ease-in-out cursor-pointer group">
      {/* Image Container */}
      <div className="relative overflow-hidden aspect-video">
        <img
          src={getCroppedImageUrl(game.background_image)}
          alt={game.name}
          className="w-full h-full object-cover"
          loading="lazy"
        />
      </div>

      {/* Content Container */}
      <div className="p-5 flex flex-col justify-between h-full">
        {/* Platforms and Score Row */}
        <div className="flex items-center justify-between mb-3">
            {game.parent_platforms && game.parent_platforms.length > 0 && (
            <div className="flex flex-wrap gap-2 text-gray-500">
                {game.parent_platforms.slice(0, 4).map((p) => (
                    <span key={p.platform.id} className="text-xl" title={p.platform.name}>
                         <span className="text-xs bg-[#303030] px-2 py-1 rounded text-gray-300">{p.platform.name}</span>
                    </span>
                ))}
            </div>
            )}
            {game.metacritic && <CriticScore score={game.metacritic} />}
        </div>


        {/* Title */}
        <h3
          className="text-2xl font-bold text-white mb-1 group-hover:text-gray-300 transition-colors"
          title={game.name}
        >
          {game.name}
        </h3>
        
        {/* Rating Row (Optional in reference, but keeping minimal) */}
        {game.rating > 0 && (
            <div className="mt-3 pt-3 border-t border-[#303030] flex items-center justify-between">
                 <span className="text-xs font-semibold text-gray-500 uppercase tracking-wide">Rating</span>
                 <div className="flex items-center gap-1">
                     <span className="text-yellow-500 text-sm">‚òÖ</span>
                     <span className="text-gray-400 text-sm font-bold">{game.rating}</span>
                 </div>
            </div>
        )}
      </div>
    </div>
  );
}
