import { type ReactNode } from 'react';

interface GameCardContainerProps {
  children: ReactNode;
}

export default function GameCardContainer({ children }: GameCardContainerProps) {
  return (
    <div className="rounded-lg overflow-hidden shadow-lg hover:shadow-2xl hover:shadow-blue-500/20 transform hover:scale-105 transition-all duration-300">
      {children}
    </div>
  );
}
